* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.nav {
  color: #fff;
  background-color: #78a382;
  justify-content: space-between;
  align-items: stretch;
  padding: 0 1rem;
  display: flex;
}

.nav ul {
  gap: 1rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav a {
  color: inherit;
  align-items: center;
  height: 100%;
  padding: .25rem;
  text-decoration: none;
  display: flex;
}

.nav li.active {
  background-color: #555;
}

.nav li:hover {
  background-color: #777;
}

.shopping-cart-icon {
  width: 40px;
  display: block;
}

.amount-of-added-products {
  color: #fff;
  text-align: center;
  background-color: red;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  font-size: 12px;
  position: absolute;
  top: 19px;
  right: 15px;
}

.logo {
  width: 90px;
}

.product-container {
  flex-flow: wrap;
  justify-content: space-evenly;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.product-card {
  cursor: pointer;
  border: 1px solid #ccc;
  flex-direction: column;
  flex-shrink: 1;
  max-width: 300px;
  margin: 10px;
  padding: 10px;
  display: flex;
}

.product-card img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.product-card h2 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 1.2em;
}

.product-card p {
  margin-bottom: 5px;
}

.add-to-cat-btn {
  color: #fff;
  cursor: pointer;
  background-color: #78a382;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
}

.add-to-cat-btn.disabled, .add-to-cat-btn.disabled:hover {
  cursor: not-allowed;
  background-color: gray;
}

.product-card button:hover {
  background-color: #5a7a61;
}

.product-card {
  border-radius: 4px;
  transition: all .2s ease-in-out;
}

.product-card:hover {
  transform: translateY(-3px);
  box-shadow: 1px 2px 8px #0003;
}

.product-description {
  background-color: #d2d2d2;
  font-size: medium;
  font-weight: lighter;
}

.shopping-cart-side {
  float: right;
  background-color: #f7f7f7;
  width: 500px;
  height: auto;
  transition: right .3s ease-in-out;
  position: relative;
  top: 0;
  right: 0;
  box-shadow: -2px 0 5px #0003;
}

.shopping-cart-container h2 {
  text-align: center;
}

.close-cart-btn {
  color: red;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 2rem;
  position: absolute;
  top: 10px;
  right: 30px;
}

.product-div {
  border: 1px solid #e8e8e8b6;
  margin-top: 10px;
}

.product-div:hover {
  background-color: #f6f6f6;
  box-shadow: 1px 2px 8px #0000003b;
}

.product-div span, p {
  font-size: 1.07rem;
  font-weight: bold;
}

.left-content {
  flex: 1;
  align-items: center;
  display: flex;
}

.right-content {
  flex-direction: column;
  justify-content: center;
  margin: 6px;
  display: flex;
}

.left-content img {
  width: 80px;
  height: auto;
  margin: 6px;
}

.recycle-bin {
  max-width: 30px;
  margin: 8px 8px 8px 5%;
}

.payment-icon {
  max-width: 30px;
  margin: 8px;
}

.success-purchase {
  background-color: #3fe658;
}

.search-input {
  color: #999;
  text-shadow: 0 0 #2a2a2abf;
  border: 2px outset #95c29e;
  border-radius: 50px;
  margin: 6px;
  padding: 9px;
  font-size: 14px;
}

.search-input:focus {
  outline: none;
}

.search-form img {
  width: 30px;
  margin: 6px;
}

.search-container {
  display: inline-block;
  position: relative;
}

.search-input {
  width: 100%;
  padding: 10px 40px 10px 10px;
}

.search-icon {
  position: absolute;
  top: 38%;
  right: 0;
  transform: translateY(-50%);
}

.sort-container {
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 50px;
  display: flex;
}

.sort-select {
  color: #78a382;
  background-color: #fff;
  border: 2px solid #78a382;
  border-radius: 6px;
  padding: 8px 12px;
  font-size: 14px;
  transition: border-color .3s;
}

.sort-select:hover {
  border-color: #4b6d53;
}

.sort-select option {
  color: #333;
  background-color: #fff;
  padding: 12px 16px;
}

